/* TABS */
.sui-tabTab 	{	display:inline-block; background-color:#999; text-align:center; user-select:none; padding-top:8px; font-weight:500; font-size:12px; color:#fff; cursor:pointer; border-left:1px solid #eee; border-right:1px solid #eee; height:24px; }
.sui-tabContent {	background-color: #eee; width:calc(100% - 50px); color:#333; padding:0 24px; line-height:22px;	}

/* CAROUSEL */
.sui-caroBox 	{ 	color:#555; font-size:14px; background-color:#fff; padding:28px 24px 0 24px; border-radius:6px;
					max-width:900px; margin:24px auto 0 auto; width:calc(100% - 60px); border:1px solid #999; min-height:190px;	}
.sui-caroHeader { 	font-style:italic; font-size:17px; margin:-140px 0 24px 0; color:#fff;	}
.sui-caroTitle 	{ 	font-size:21px; margin:-12px 0 8px -8px; }
.sui-caroButL 	{ 	position:relative; left:-46px; top:55px;font-size:26px; color:#fff; border-radius:4px 0 0 4px; opacity:.5; color:#ddd; background-color:#5d68cc; padding:4px;
					width:30px; cursor:pointer;	} .sui-caroButL:hover { opacity: .8 }
.sui-caroButR 	{ 	position:relative; left:calc(100% + 12px); top:13px;font-size:26px; color:#fff; border-radius:0 4px 4px 0; opacity:.5; color:#ddd; background-color:#5d68cc; padding:4px;
					width:30px; cursor:pointer;	} .sui-caroButR:hover { opacity: .8 }
.sui-caroView 	{	font-style:italic; font-size:16px; }
.sui-caroText 	{	border-left:5px solid #eee; padding-left:24px; font-family:"EB Garamond",serif,shanticon; font-weight:400; line-height:22px; font-size:16px; }
.sui-caroPic 	{	max-width:320px; height:172px; }
.sui-caroLeft 	{	display:inline-block; width:calc(100% - 330px); vertical-align:top; margin-right:24px;	}
.sui-caroRes 	{	display:inline-block; font-size:14px; font-weight:400; margin-top:8px; }
.sui-caroDots 	{	display:inline-block; width:calc(66% - 200px); text-align:center; min-width:200px;	}
.sui-caroDot 	{	display:inline-block; opacity:.8; width:8px; height:8px; border-radius:8px; border:1px solid #999; margin-right:16px;
					} .sui-caroDot:hover {	 background-color:#5d68cc; }

/* MISC PAGES */
.sui-breadCrumbs {  display:inline-block; font-size:13px; margin-left:24x; cursor:pointer; }
.sui-crumb:hover { 	text-decoration:underline;	}
.sui-popItem	 {  color:#ddd;
					} .sui-popItem:hover{ color:#a4baec; }
.sui-popover 	 { 	position:absolute; width:300px; min-height: 100px; padding:12px; font-size:13px; border-radius:4px; z-index:1001;
					border: 1px solid #999; background-color:#fff; box-shadow: 0 5px 10px rgb(0,0,0,.2); }
.sui-pageLab	 { 	font-weight:400; letter-spacing: 0.0475rem; font-size:13px;	}
.sui-pageVal	 { 	word-wrap:break-word; font-family:"EB Garamond",serif; font-weight:400; font-size:17px; 		}
.sui-pageThumb 	 { 	display:inline-block; height:50px; margin-bottom:4px; cursor:pointer; border-radius:3px; border: 2px solid #000;
					}.sui-pageThumb:hover { border-color:#fff; }


/* PSUEDO ELEMENTS */

.sui-main ::-webkit-scrollbar { width: 10px; height:8px }
.sui-main ::-webkit-scrollbar-track { background: transparent; border-left: 1px solid #aaa}
.sui-main ::-webkit-scrollbar-thumb { border-radius:8px ;background:#ccc }
.sui-main ::-webkit-scrollbar-thumb:hover { background: #aaa }
.sui-main ::-webkit-input-placeholder { color: #bbb }	::placeholder { color: #bbb }

a { text-decoration:none; }
.sui-noA { border-bottom:hidden; cursor:default; color:#333 }

#router { float: left; }

.shanticon-terms:before {
	content: "\e635";
}

/* ys2n: action css */
.open { display: block; }
.closed { display: none; }

/* custom site styles */
.mandala.sui-header{ background-color:#4d59ca; }
.mandala.sui-results {
	background-size: 100% auto;
	background-repeat: no-repeat;
	background-image: url('https://cicada.shanti.virginia.edu/images/mandala/shanti-image-517836/full/!3000,/0/default.jpg');
}

.mandala > .sui-contentHead > div {
	margin-top: .5em;
}

/* facetbox open/close sui-advEdit (part of sui-advBox) */
/* TODO: refactor to "sliding" effect css */

/* override plain .open .closed to customize effect
   TODO: add slide-in/out css transition
*/

.sui-adv.open {
	display: block;
}

.sui-adv.closed {
	display: none;
}

.sui-nameEntry span {
	margin-right: .2em;
}

.sui-nameEntry {
	list-style-type: none;
}

.sui-definitionEntry {
	list-style-type: none;
}

.sui-definitionEntry-other {
	list-style-type: square;
}

.sui-definitionEntry-other li {
	margin-bottom: 1em;
}

.sui-definitionEntry-term {
	margin-bottom: 1em;

}

.sui-nameEntry li:before {
	content: "> ";
	margin-right: 0em;
}

.sui-nameEntry li:last-child {
	margin-bottom: .5em;
}

.sui-nameEntry-header {
	font-weight: bolder;
}

.sui-nameEntry-meta {
	font-style: italic;
	font-weight: lighter;
}

.sui-nameEntry-writing-system:before {
	content: "(";
}

.sui-nameEntry-writing-system:after {
	content: ")";
}

.sui-audioPlayer button {
	font-family: 'shanticon';
	padding: .3em;
	padding: .3em;
}

.sui-color-terms {color: rgb(162, 115, 63)}
.sui-color-all { color: rgb(91, 102, 203)}
.sui-color-audio-video { color: rgb(88, 170, 180)}
.sui-color-images {  color: rgb(180, 156, 89)}
.sui-color-sources { color: rgb(90, 87, 173)}
.sui-color-texts { color: rgb(139, 90, 161)}
.sui-color-visuals { color: rgb(110, 148, 86)}
.sui-color-subjects { color: rgb(204, 76, 57)}
.sui-color-collections { color: rgb(91, 102, 203)}



.zoom { transition: transform .2s }
.zoom:hover { transform: scale(1.2); z-index: 100 }

.zoom > img {
	object-fit: cover;
	height: 9rem;
	max-height: 14rem;
	max-width: 19rem;
	width: 99%;
}


